@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700|Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.panel {
  padding: 10px;
}

.horizontal-display > ul > li {
  display: block;
  float: right;
  margin-right: 15px;
}

.icon-footer {
  color: black;
  transition: 0.2s ease-in !important;
}
.icon-footer:hover {
  color: #f03a17;
}

html,
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

